<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon dark @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة درس جديد</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-row>
                  <v-col>
                     <v-text-field
                        outlined
                        label="السؤال"
                        v-model="videoQuiz.quizQuestion"
                     ></v-text-field>
                  </v-col>
               </v-row>
               <v-row>
                  <v-col
                     cols="4"
                     v-for="(answer, index) in videoQuiz.Choice"
                     :key="index"
                  >
                     <v-text-field
                        outlined
                        :label="
                           answer.correctAnswer
                              ? 'الجواب الصحبح'
                              : 'الجواب الغير صحيح'
                        "
                        v-model="answer.questionChoice"
                     ></v-text-field>
                  </v-col>
               </v-row>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="primary" text @click="saveQuiz"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip left color="primary">
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               icon
               color="primary"
               v-bind="attrs"
               v-on="on"
               @click="show = true"
            >
               <v-icon> mdi-help-circle</v-icon>
            </v-btn>
         </template>
         <span>سؤال الدرس</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      quiz: Array,
      videoId: Number,
   },
   data: () => ({
      show: false,
      videoQuiz: {
         quizQuestion: "",
         Choice: [
            {
               correctAnswer: true,
               questionChoice: "",
            },
            {
               correctAnswer: false,
               questionChoice: "",
            },
            {
               correctAnswer: false,
               questionChoice: "",
            },
         ],
      },
   }),
   created() {
      if (this.quiz.length > 0) {
         this.videoQuiz.createdAt = this.quiz[0].createdAt;
         this.videoQuiz.createdBy = this.quiz[0].createdBy;
         this.videoQuiz.idQuiz = this.quiz[0].idQuiz;
         this.videoQuiz.quizQuestion = this.quiz[0].quizQuestion;
         this.videoQuiz.videoId = this.quiz[0].videoId;
         if (this.quiz[0].Choice.length > 0) {
            this.videoQuiz.Choice = this.quiz[0].Choice;
         } else {
            this.videoQuiz.Choice = [
               {
                  correctAnswer: true,
                  questionChoice: "",
               },
               {
                  correctAnswer: false,
                  questionChoice: "",
               },
               {
                  correctAnswer: false,
                  questionChoice: "",
               },
            ];
         }
      } else {
         this.videoQuiz = {
            quizQuestion: "",
            Choice: [
               {
                  correctAnswer: true,
                  questionChoice: "",
               },
               {
                  correctAnswer: false,
                  questionChoice: "",
               },
               {
                  correctAnswer: false,
                  questionChoice: "",
               },
            ],
         };
      }
   },
   methods: {
      saveQuiz() {
         let self = this;
         if (this.videoQuiz?.idQuiz) {
            self.$http
               .put(
                  `${self.$store.state.apiUrl}/quiz/${this.videoQuiz.idQuiz}`,
                  {
                     quizQuestion: this.videoQuiz.quizQuestion,
                     videoId: this.videoQuiz.videoId,
                     createdBy: this.videoQuiz.createdBy,
                  }
               )
               .then((res) => {
                  if (this.videoQuiz.Choice[0]?.quizId) {
                     console.log(this.videoQuiz.Choice[1]);
                     self.$http
                        .put(
                           `${self.$store.state.apiUrl}/choice/${this.videoQuiz.Choice[0].idChoice}`,
                           this.videoQuiz.Choice[0]
                        )
                        .then((res) => console.log(res));
                     self.$http
                        .put(
                           `${self.$store.state.apiUrl}/choice/${this.videoQuiz.Choice[1].idChoice}`,
                           this.videoQuiz.Choice[1]
                        )
                        .then((res) => console.log(res));
                     self.$http
                        .put(
                           `${self.$store.state.apiUrl}/choice/${this.videoQuiz.Choice[2].idChoice}`,
                           this.videoQuiz.Choice[2]
                        )
                        .then((res) => {
                           console.log(res);
                           this.show = false;
                           this.$toasted.success("تم حفظ البيانات");
                        });
                  } else {
                     let choice = [
                        {
                           correctAnswer: true,
                           questionChoice:
                              this.videoQuiz.Choice[0].questionChoice,
                           quizId: this.videoQuiz.idQuiz,
                        },
                        {
                           correctAnswer: false,
                           questionChoice:
                              this.videoQuiz.Choice[1].questionChoice,
                           quizId: this.videoQuiz.idQuiz,
                        },
                        {
                           correctAnswer: false,
                           questionChoice:
                              this.videoQuiz.Choice[2].questionChoice,
                           quizId: this.videoQuiz.idQuiz,
                        },
                     ];

                     self.$http
                        .post(
                           `${self.$store.state.apiUrl}/addMultiChoice`,
                           choice
                        )
                        .then((quizRes) => {
                           console.log(quizRes);
                           this.show = false;
                           this.$toasted.success("تم حفظ البيانات");
                        });
                  }
               });
         } else {
            let quiz = {
               quizQuestion: this.videoQuiz.quizQuestion,
               videoId: self.videoId,
               createdBy: self.$store.state.user.idUser,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/addQuiz`, quiz)
               .then((res) => {
                  console.log(res.data);

                  let choice = [
                     {
                        correctAnswer: true,
                        questionChoice: this.videoQuiz.Choice[0].questionChoice,
                        quizId: res.data.idQuiz,
                     },
                     {
                        correctAnswer: false,
                        questionChoice: this.videoQuiz.Choice[1].questionChoice,
                        quizId: res.data.idQuiz,
                     },
                     {
                        correctAnswer: false,
                        questionChoice: this.videoQuiz.Choice[2].questionChoice,
                        quizId: res.data.idQuiz,
                     },
                  ];

                  console.log(choice);
                  self.$http
                     .post(`${self.$store.state.apiUrl}/addMultiChoice`, choice)
                     .then((quizRes) => {
                        console.log(quizRes);
                        this.show = false;

                        this.$toasted.success("تم حفظ البيانات");
                     });
               })
               .catch((e) => {
                  this.$toasted.error("حدث خطأ في حفظ البيانات");
                  this.show = false;
               });
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
