import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
   state: {
      apiUrl: "https://api.kattwa.com/api",
      // apiUrl: "http://localhost:5100/api",
      selectedYear: 0,
      user: null,
      courses: [],
      courseDetails: null,
   },
   mutations: {
      GET_USER_DATA(state, userData) {
         state.user = userData;
      },
   },
   actions: {
      getUserDate({ commit }) {
         let data = localStorage.getItem("teacher");
         commit("GET_USER_DATA", JSON.parse(data));
      },
   },
   modules: {},
});
