<template>
   <div>
      <v-overlay :value="overlay">
         <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="10">
                  <h3 class="warning-color">الكورسات</h3>
               </v-col>
               <v-col cols="2" style="text-align: center">
                  <!-- <AddInside
                     @reloadTask="initialData()"
                     style="display: inline-block"
                  />

                  <SearchInside
                     style="display: inline-block"
                     @clicked="onSearch"
                  /> -->
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               الكورسات
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>
            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="15"
               class="table-content table-warning-color"
            >
               <template v-slot:item.isActive="{ item, index }">
                  <v-avatar size="20" color="success" v-if="item.isActive == 1">
                  </v-avatar>

                  <v-avatar size="20" color="error" v-else> </v-avatar>
               </template>

               <template v-slot:item.index="{ item, index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>
               <template v-slot:item.image="{ item, index }">
                  <v-avatar size="32" v-if="item.image != null">
                     <img :src="item.image" alt="John" />
                  </v-avatar>

                  <v-avatar size="32" color="indigo" v-else>
                     <v-icon dark> mdi-account-circle </v-icon>
                  </v-avatar>
               </template>
               <template v-slot:item.coursePath="{ item }">
                  <img
                     :src="`${$store.state.apiUrl}/${item.coursePath}`"
                     width="70"
                  />
               </template>
               <template v-slot:item.actions="{ item }">
                  <!-- <EditInside
                     style="display: inline-block"
                     :dataToEdit="item"
                     @reloadTask="initialData()"
                  />

                  <Images :images="item.images" style="display: inline-block" />

                  <v-btn icon @click="dialog = false">
                     <DeleteInside
                        :dataToDelete="item"
                        @reloadTask="initialData()"
                     />
                  </v-btn> -->
                  <v-btn icon @click="goToCourse(item)">
                     <v-icon color="primary"> mdi-eye </v-icon>
                  </v-btn>
               </template>
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
export default {
   data: () => ({
      items: [],
      search: "",
      overlay: false,
      headers: [
         { text: "التسلسل", value: "index" },
         {
            text: "صورة الكورس",
            value: "coursePath",
         },
         { text: "اسم الكورس", value: "courseTitle" },
         { text: "الصف", value: "class.className" },
         { text: "المادة", value: "subject.subjectName" },
         { text: "سعر الكورس", value: "coursePrice" },
         {
            text: "تقييم الكورس",
            align: "start",
            value: "courseRate",
         },
         { text: "الاجراءات", value: "actions" },
      ],
   }),
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         this.overlay = true;
         this.$http
            .get(
               `${this.$store.state.apiUrl}/courseMoney/${this.$store.state.user.idUser}`
            )
            .then((res) => {
               console.log(res.data);
               this.items = res.data;
               this.overlay = false;
            });
      },
      goToCourse(item) {
         console.log(item);
         this.$http
            .get(`${this.$store.state.apiUrl}/singleCourse/${item.idCourse}`)
            .then((res) => {
               console.log(res.data);
               this.$store.state.courseDetails = res.data;
               this.$router.push("/details").catch(() => {});
            });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.items.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.courseTitle.toLowerCase().includes(v));
            });
         } else {
            return this.items;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
