<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-overlay :value="loading">
               <v-progress-circular
                  indeterminate
                  size="64"
               ></v-progress-circular>
               <p>جاري رفع الفيديو للخادم...</p>
            </v-overlay>
            <v-toolbar dark color="primary">
               <v-btn icon dark @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة درس جديد</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-row>
                  <v-col cols="6">
                     <v-text-field
                        solo
                        v-model="courseTitle"
                        label="عنوان الدرس"
                     ></v-text-field>

                     <label>
                        تاريخ الفتح
                        <v-text-field
                           v-model="unlockAt"
                           type="date"
                           label="اختر التاريخ"
                           solo
                        ></v-text-field>
                     </label>
                  </v-col>
                  <v-col cols="6">
                     <v-file-input
                        label="فيديو الدرس"
                        solo
                        v-model="videoUpload"
                        @change="getAllFilesVideo"
                     ></v-file-input>

                     <v-file-input
                        label="صورة الدرس"
                        solo
                        v-model="files"
                        @change="getAllFiles"
                     ></v-file-input>
                  </v-col>
               </v-row>

               <v-textarea
                  solo
                  rows="3"
                  row-height="15"
                  name="input-7-4"
                  v-model="videoDescription"
                  label="وصف الدرس"
               ></v-textarea>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="primary" text @click="saveOuterData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة درس جديد</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      datePickerAddModel: false,
      show: false,
      loading: false,
      courseTitle: "",
      videoImage: "",
      unlockAt: "",
      videoDescription: "",
      video: "",
      files: [],
      videoUpload: [],
   }),
   created() {},
   methods: {
      getAllFiles() {
         let self = this;
         let formData = new FormData();
         formData.append("files", self.files);

         self.$http
            .post(`${self.$store.state.apiUrl}/upload`, formData)
            .then((res) => {
               self.videoImage = res.data;
               console.log(res.data);
            });
      },
      getAllFilesVideo() {
         let self = this;
         self.loading = true;
         let formData = new FormData();
         formData.append("video", self.videoUpload);
         self.$http
            .post(`${this.$store.state.apiUrl}/uploadVideo`, formData, {
               headers: {
                  "Content-Type": "multipart/form-data",
               },
            })
            .then((res) => {
               self.loading = false;
               self.video = res.data;
               console.log(res.data);
            })
            .catch((e) => {
               self.loading = false;
               this.$toasted.error("حدث خطأ في رفع الفيديو");
            });
      },
      getDates() {
         this.datePickerSearchModel = false;
         this.datePickerAddModel = false;
      },
      saveOuterData() {
         let self = this;

         if (
            self.unlockAt &&
            self.courseTitle &&
            self.videoDescription &&
            self.videoImage &&
            self.video
         ) {
            let dateUnlock = new Date(
               `${new Date(self.unlockAt).getFullYear()}-${
                  new Date(self.unlockAt).getMonth() + 1
               }-${new Date(self.unlockAt).getDate()}`
            );
            let videoData = {
               videoTitle: self.courseTitle,
               videoLink: "https://youtu.be/FP6X4MFR3Aw",
               videoDescription: self.videoDescription,
               courseId: self.$store.state.courseDetails.idCourse,
               createdBy: self.$store.state.user.idUser,
               unlockAt: dateUnlock,
               secondVideoLink: self.video.videoPath,
               videoPhoto: self.videoImage.imagePath,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/addCourseVideo`, videoData)
               .then((res) => {
                  console.log(res.data);
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }

         this.show = false;
      },
   },
};
</script>

