<template>
   <v-app>
      <v-app-bar app color="primary" v-if="$store.state.user != null">
         <div class="d-flex align-center" style="width: 100%">
            <div>
               <v-img
                  style="background: #f0f5f9; border-radius: 10px"
                  :src="require('./assets/logo.png')"
                  class="my-3"
                  contain
                  height="50"
               />
            </div>
            <v-spacer></v-spacer>
            <div>
               <v-list
                  class="justify-space-around d-flex"
                  dense
                  color="primary"
               >
                  <v-list-item-group v-model="selectedItem" class="d-flex">
                     <v-list-item
                        v-for="(item, i) in items"
                        :key="i"
                        style="margin: 0 10px"
                        color="white"
                        @click="page(item)"
                     >
                        <v-list-item-icon>
                           <v-icon v-text="item.icon" color="white"></v-icon>
                        </v-list-item-icon>

                        <v-list-item-content>
                           <v-list-item-title
                              v-text="item.text"
                              style="color: #ffffff"
                           ></v-list-item-title>
                        </v-list-item-content>
                     </v-list-item>
                  </v-list-item-group>
               </v-list>
            </div>

            <v-spacer></v-spacer>
            <div>
               <v-menu rounded="lg" offset-y>
                  <template v-slot:activator="{ attrs, on }">
                     <v-btn
                        elevation="0"
                        color="primary"
                        class="white--text"
                        v-bind="attrs"
                        v-on="on"
                     >
                        <v-icon left color="white"> mdi-account-circle </v-icon>
                        <span style="color: #d6e0f0; font-size: 12px">
                           {{ $store.state.user.userName }}
                        </span>
                     </v-btn>
                  </template>

                  <v-list style="padding: 0px">
                     <v-list-item link @click="logout">
                        <v-list-item-title
                           style="color: #52616b; font-size: 12px"
                        >
                           <v-icon left color="primary">
                              mdi-location-exit
                           </v-icon>
                           تسجيل خروج
                        </v-list-item-title>
                     </v-list-item>
                  </v-list>
               </v-menu>
            </div>
         </div>
      </v-app-bar>

      <v-main>
         <router-view />
      </v-main>
   </v-app>
</template>

<script>
export default {
   name: "App",

   data: () => ({
      selectedItem: 0,
      items: [
         { title: "/", text: "الرئيسية", icon: "mdi-home" },
         {
            title: "Courses",
            text: "الكورسات",
            icon: "mdi-monitor-dashboard",
         },
      ],
   }),
   created() {
      this.$store
         .dispatch("getUserDate")
         .then(() => {
            if (this.$store.state.user) {
               this.$router.push("/").catch(() => {});
            } else {
               this.$router.push("/login").catch((e) => {});
            }
         })
         .catch(() => {});
   },
   methods: {
      page(item) {
         this.$router.push(item.title).catch(() => {});
      },
      logout() {
         localStorage.removeItem("teacher");
         location.reload();
      },
   },
};
</script>
<style>
@import url("./font.css");
</style>
