<template>
   <div>
      <v-container class="container" fluid v-if="courseDetails != null">
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="10">
                  <h3 class="warning-color">تفاصيل الكورس</h3>
               </v-col>

               <v-col cols="2" style="text-align: left">
                  <AddVideoCourse
                     @reloadTask="initialData()"
                     style="display: inline-block"
                  />
                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           class="mx-2"
                           fab
                           small
                           color="primary"
                           @click="$router.go(-1)"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-arrow-left </v-icon>
                        </v-btn>
                     </template>
                     <span>رجوع</span>
                  </v-tooltip>
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               تفاصيل الكورس
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>
            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="15"
               class="table-content table-warning-color"
            >
               <template v-slot:item.isActive="{ item, index }">
                  <v-avatar size="20" color="success" v-if="item.isActive == 1">
                  </v-avatar>

                  <v-avatar size="20" color="error" v-else> </v-avatar>
               </template>

               <template v-slot:item.index="{ item, index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>
               <template v-slot:item.videoPhoto="{ item }">
                  <img
                     :src="`${$store.state.apiUrl}/${item.videoPhoto}`"
                     width="70"
                  />
               </template>
               <template v-slot:item.actions="{ item }"> </template>

               <template v-slot:item.createdAt="{ item }">
                  {{ formatDate(item.createdAt) }}
               </template>
               <template v-slot:item.unlockAt="{ item }">{{
                  formatDate(item.unlockAt)
               }}</template>

               <template v-slot:item.actions="{ item }">
                  <AddQuestion
                     :quiz="item.Quiz"
                     :videoId="item.idCourseVideo"
                     @reloadTask="initialData()"
                     style="display: inline-block"
                  />
                  <v-btn icon color="error">
                     <v-icon>mdi-delete</v-icon>
                  </v-btn>
               </template>
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
import AddVideoCourse from "./../components/AddVideoCourse.vue";
import AddQuestion from "./../components/AddQuestion.vue";
export default {
   components: {
      AddVideoCourse,
      AddQuestion,
   },
   data: () => ({
      courseDetails: null,
      courseVideo: [],
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "صورة الدرس", value: "videoPhoto" },
         { text: "عنوان الدرس", value: "videoTitle" },
         { text: "تاريخ الاضافة", value: "createdAt" },
         { text: "تاريخ فتح الدرس", value: "unlockAt" },
         { text: "وصف الدرس", value: "videoDescription" },
         { text: "الاجراء", value: "actions" },
      ],
   }),
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         this.$http
            .get(
               `${this.$store.state.apiUrl}/singleCourse/${this.$store.state.courseDetails.idCourse}`
            )
            .then((res) => {
               console.log(res.data);
               this.$store.state.courseDetails = res.data;
               this.courseDetails = this.$store.state.courseDetails;
               this.courseVideo = this.$store.state.courseDetails.CourseVideo;
            });
      },
      formatDate(date) {
         let receivedDate = new Date(date);
         let format = receivedDate.toISOString().split("-");
         return `${format[2].slice(0, 2)}/${format[1]}/${format[0]}`;
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.courseVideo.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.videoTitle.toLowerCase().includes(v));
            });
         } else {
            return this.courseVideo;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
