<script>
// @ is an alias to /src
import SectionChart from "./../components/SectionChart.vue";
import CategoryChart from "./../components/CategoryChart.vue";
export default {
   components: {
      SectionChart,
      CategoryChart,
   },
   name: "Home",
   data: () => ({
      allCourses: [],
      courseCount: 0,
      videoCount: 0,
      studentCount: 0,
   }),
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         this.$http
            .get(
               `${this.$store.state.apiUrl}/courseMoney/${this.$store.state.user.idUser}`
            )
            .then((res) => {
               console.log(res.data);
               this.$store.state.courses = res.data;
            });

         this.$http
            .get(
               `${this.$store.state.apiUrl}/userHome/${this.$store.state.user.idUser}`
            )
            .then((res) => {
               if (res.data.Course.length) {
                  let countVideos = 0;
                  let countStudent = 0;
                  this.courseCount = res.data.Course.length;
                  res.data.Course.map((c) => {
                     countVideos += c.CourseVideo.length;
                     countStudent += c.StudentCourse.length;
                  });

                  this.videoCount = countVideos;
                  this.studentCount = countStudent;
               }
            });
      },
   },
};
</script>
